var iconv = require('iconv-lite');
var Buffer = require('buffer/').Buffer
const getDecodedText = ({RequestType, Decode}, text, type) => {
    let result = Decode.length == 0 ? text : RequestType.length > 0 ?
        iconv.decode(new Buffer(text, type || 'base64'), Decode)
        : new Buffer(text, Decode).toString()
    // console.log({RequestType, Decode, result, text})
    return result
}
