        //                 config: result.type,
        //                 hrefs: JSON.parse(result.hrefs.replace(/\'/g, '\"')),
        //                 texts: JSON.parse(result.texts.replace(/\'/g, '\"')),
        //                 images: JSON.parse(result.images.replace(/\'/g, '\"')),
        //             }
