import moment from "moment";
import Cheerio from 'cheerio'
import LERequest from './LERequest'

const ConsoleSwitch = false

export function evalTemplate(template, context) {
    let execution = null;
    ConsoleSwitch && console.log('~~~~~~~~~~~>Begin with template:', template)
    let user = {}

    try {
        let bindContext = (function () {
            return eval(template);
        }).bind({
            user: user,
            context: context,
            moment: moment,
            assign: assign,
            convertResponse: convertResponse,
            request: appRequest,// new LERequest(global.host, Config.headers()),
            Cheerio: Cheerio,
        });
        execution = bindContext();
        bindContext = undefined;
    } catch (ex) {
        ConsoleSwitch && console.log('~~~~~~~~~~~>Eval exception:' + ex)
    }
    if (typeof execution != "function") {
        execution = () => template;
    }
    ConsoleSwitch && console.log('~~~~~~~~~~~>Eval return(function):', execution)
    return execution;
}

export function appRequest(url, headers = undefined, type, success, error,
                           requestType = 'get',
                           requestData = {},
                           dataType,
                           timeout) {
    new LERequest('', headers, type).api(url).success(function (status, response) {
        success && success(status, response);
    }).error(function (e) {
        error && error(e);
    }).run({
        type: requestType,
        data: requestData, dataType, timeout
    })
}

//
export function explainValueTemplate(template) {
    if (typeof template === "string") {
        let matches = template.match(/%\{.*?\}/ig);
        if (!!matches) {
            return matches.map(e => {
                return {
                    raw: e,
                    valueId: e.replace("%{", "").replace("}", "")
                };
            })
        }
    }
    return [];
}

export function replaceWithExplainedValue(explainedValue, raw, data) {
    let value = raw;
    if (explainedValue.length > 0) {
        explainedValue.forEach(p => {
            value = value.replace(p.raw, data[p.valueId] || "");
        });
    }
    return value;
}

export function convertResponse(response) {
    let contentType = response.headers.get("Content-Type").replace(/;.*charset=.*/ig, "");
    switch (contentType) {
        case "text/html":
        case "text/plain":
            return response.text();
        case "application/json":
            return response.json();
        default:
            return response.blob();
    }
}

//

export function format(val) {
    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

export function parse(val) {
    return val.toString().replace(/\$\s?|(,*)/g, '');
}

export function assign(p) {
    let c;
    if ((typeof p === 'object' && p !== null)) {
        if ((Array.isArray(p)))
            c = [];
        else
            c = {};
        for (var i in p) {
            if (typeof p[i] === 'object' && !i.indexOf("_") !== 0) {
                c[i] = this.assign(p[i]);
            } else {
                c[i] = p[i];
            }
        }
    } else {
        c = p;
    }
    return c;
}

export function empty(o) {
    if (o === undefined || o === "" || o === null)
        return true;
    return false;
}

export function getStringLen(str) {
    if (str === null || str === undefined) return 0;
    if (typeof str != "string") {
        str += "";
    }
    return str.replace(/[^\x00-\xff]/g, "01").length;
}