import notification from 'antd/lib/notification'
import Utils from './LEUtils'

const ConsoleON = true
export const LEResponseStatus = {
    Idle: '',
    Timeout: '408',
    Success: '1',
    Failure: '0',
}
export default class LERequest {
    constructor(apiHost, defaultHeaders) {
        let context = this
        context.uuid = Utils.uuid(16)
        context.onSuccess = undefined
        context.onError = undefined
        context.apiHost = undefined
        context.method = undefined
        context.endPoint = undefined
        context.data = undefined
        context.dataType = undefined
        context.defaultHeaders = undefined
        context.enableToast = true
        context.methodMap = {
            0: "HEAD",
            1: "GET",
            2: "DELETE",
            3: "POST",
            4: "PUT",
            5: "PATCH"
        }
        context.apiHost = apiHost
        context.defaultHeaders = defaultHeaders || {}
        context.timeout = 3000
        context.responseStatus = LEResponseStatus.Idle
    }

    api(endPoint, toast) {
        let context = this
        context.endPoint = endPoint
        context.enableToast = toast || context.enableToast
        return context
    }

    success(func) {
        let context = this
        context.onSuccess = func
        return context
    }

    error(func) {
        let context = this
        context.onError = func
        return context
    }

    head(timeout) {
        let context = this
        context.method = 0
        context.timeout = timeout || context.timeout
        context.exec()
        return context
    }

    get(timeout) {
        let context = this
        context.method = 1
        context.timeout = timeout || context.timeout
        context.exec()
        return context
    }

    delete(timeout) {
        let context = this
        context.method = 2
        context.timeout = timeout || context.timeout
        context.exec()
        return context
    }

    post(data, dataType, timeout) {
        let context = this
        context.method = 3
        context.timeout = timeout || context.timeout
        context.data = data || {}
        context.dataType = dataType || LERequest.dataTypeMap.urlForm
        context.exec()
        return context
    }

    put(data, dataType, timeout) {
        let context = this
        context.method = 4
        context.timeout = timeout || context.timeout
        context.data = data || {}
        context.dataType = dataType || LERequest.dataTypeMap.urlForm
        context.exec()
        return context
    }

    patch(data, dataType, timeout) {
        let context = this
        context.method = 5
        context.timeout = timeout || context.timeout
        context.data = data || {}
        context.dataType = dataType || LERequest.dataTypeMap.urlForm
        context.exec()
        return context
    }

    run({type, data, dataType, timeout}) {
        let context = this
        switch (type) {
            case 'get':
                return context.get(timeout, dataType)
            case 'head':
                return context.head(timeout)
            case 'delete':
                return context.delete(timeout)
            case 'post':
                return context.post(data, dataType, timeout)
            case 'put':
                return context.put(data, dataType, timeout)
            case 'patch':
                return context.patch(data, dataType, timeout)
        }
    }

    cancelRequest() {
        let context = this
        if (context.responseStatus == LEResponseStatus.Idle) {
            context.requestPromise && context.requestPromise.abort && context.requestPromise.abort()
        }
        return context
    }

    warp_fetch(fetch_promise) {
        let context = this
        let timeout_fn = null;
        let abort = null;
        //promise
        let timeout_promise = new Promise(function (resolve, reject) {
            timeout_fn = function () {
                if (context.responseStatus == LEResponseStatus.Idle) {
                    context.responseStatus = LEResponseStatus.Timeout
                    let timeOutError = {
                        statusCode: LEResponseStatus.Timeout,
                        msg: '',
                        url: context.apiHost + context.endPoint,
                        timeout: context.timeout,
                        uuid: context.uuid,
                    }
                    reject(timeOutError);
                    if (context.onError) {
                        context.onError(timeOutError)
                    } else if (context.enableToast) {
                        notification.error({
                            message: ''
                        })
                    }
                    ConsoleON && console.log(':', {
                        timeOutError,
                        url: context.apiHost + context.endPoint
                    })
                }
            };
        });
        //promise
        let abort_promise = new Promise(function (resolve, reject) {
            abort = function () {
                if (context.responseStatus == LEResponseStatus.Idle) {
                    context.responseStatus = LEResponseStatus.Abort
                    reject({
                        statusCode: -1,
                        msg: '',
                        url: context.apiHost + context.endPoint,
                        timeout: context.timeout,
                        uuid: context.uuid,
                    })
                }
            };
        });
        //
        let abortable_promise = Promise.race([
            fetch_promise,
            timeout_promise,
            abort_promise,
        ]);
        //
        setTimeout(timeout_fn, context.timeout);
        //
        abortable_promise.abort = abort;
        return abortable_promise;
    }

    execFetch() {
        let context = this
        let headers = new Headers(context.defaultHeaders)
        let request = {
            method: this.methodMap[context.method],
            // mode: "cors",
            // credentials: "include",
        }
        if (context.method > 2) {
            if (!headers.get("Content-Type")) {
                ConsoleON && console.log('head', headers.get("Content-Type"))
                headers.append("Content-Type", context.dataType)
            }
            if (context.dataType == LERequest.dataTypeMap.urlForm && context.data instanceof Object) {
                context.data = context.urlParam(context.data)
            } else if (context.dataType == LERequest.dataTypeMap.json && context.data instanceof Object) {
                context.data = JSON.stringify(context.data)
            }
            request["body"] = context.data
        }
        request["headers"] = headers

        let api = context.apiHost + context.endPoint
        ConsoleON && console.log('api', api, request)
        context.requestPromise = context.warp_fetch(new Promise((resolve, reject) => {
            fetch(api, request).then(function (response) {
                resolve(response)
            }).catch(function (error) {
                reject(error)
            })
        })).then(function (response) {
            if (response.status >= 200 && response.status <= 300) {
                if (context.responseStatus == LEResponseStatus.Idle) {
                    context.responseStatus = LEResponseStatus.Success
                    context.onSuccess(response.status, response, context.uuid)
                }
            } else {
                response.json().then(function (json) {
                    let msg = json && json.errormsg && json.errormsg.length > 0 ? json.errormsg : undefined
                    if (msg) {
                        if (context.responseStatus == LEResponseStatus.Idle) {
                            context.responseStatus = LEResponseStatus.Failure
                            if (context.onError) {
                                context.onError(msg)
                            } else if (context.enableToast) {
                                notification.error({
                                    message: msg
                                })
                            }
                        }
                        ConsoleON && console.log('ErrorMsg from server:', {
                            msg,
                            status: response.status,
                            url: context.apiHost + context.endPoint
                        })
                    }
                }).catch(function (error) {
                    let msg = ""
                    if (context.responseStatus == LEResponseStatus.Idle) {
                        context.responseStatus = LEResponseStatus.Failure
                        if (context.onError) {
                            context.onError(msg)
                        } else if (context.enableToast) {
                            notification.error({
                                message: msg
                            })
                        }
                    }
                    ConsoleON && console.log('Unknown Error:', {
                        error,
                        msg,
                        status: response.status,
                        url: context.apiHost + context.endPoint
                    })
                })
            }
        }).catch(function (error) {
            if (context.responseStatus == LEResponseStatus.Idle) {
                context.responseStatus = LEResponseStatus.Failure
                if (context.onError) {
                    context.onError(error, context.uuid)
                    ConsoleON && console.log('status:', error, context.apiHost, context.endPoint)
                } else if (context.enableToast) {
                    notification.error({
                        message: ""
                    })
                    ConsoleON && console.log('status:', '', context.apiHost, context.endPoint)
                }
            }
        })
    }

    exec() {
        this.execFetch()
    }

    urlParam(params) {
        return Object
            .keys(params)
            .map((key) => {
                return encodeURIComponent(key) + '=' + encodeURIComponent(params[key])
            })
            .join('&')
    }
}
LERequest.dataTypeMap = {
    urlForm: 'application/x-www-form-urlencoded;charset=UTF-8',
    multipartForm: "multipart/form-data",
    json: "application/json;charset=UTF-8",
}