import React, {createContext, PureComponent} from 'react';

export const initialStore = {
    savedLinks: [],
    historyList: [],
    showWKY: false,
    showImage: false,
    downloadsStorage: [],
};
const StoreKeyLinks = 'StoreKeyLinks'
export const Store = createContext(initialStore);

export class StoreProvider extends PureComponent {

    state = {
        ...initialStore
    }

    toggleWKY = (flag) => {
        let context = this
        let result = flag === undefined ? !context.state.showWKY : flag
        context.setState({showWKY: result})
    }
    toggleImage = (flag) => {
        let result = flag === undefined ? !this.state.showImage : flag
        this.setState({showImage: result})
        return result
    }
    addDownload = (download) => {
        const {downloadsStorage} = this.state
        const index = downloadsStorage.findIndex(d => d.id === download.id)
        if (index >= 0) {
            downloadsStorage.splice(index, 1)
        }
        let array = [download, ...downloadsStorage]
        this.setState({downloadsStorage: array})
    }
    removeDownloads = (downloads) => {
        downloads = Array.isArray(downloads) ? downloads : [downloads]
        const {downloadsStorage} = this.state;
        const _downloadsStorage = downloadsStorage.filter(ds => !downloads.find(d => (d.id || d) === ds.id));
        this.setState({downloadsStorage: _downloadsStorage});
    }
    refreshDownloads = (downloads) => {
        let {downloadsStorage} = this.state
        let _downloadsStorage = []
        downloadsStorage.forEach(download => {
            const index = downloads.findIndex(d => d.id === download.id)
            if (index >= 0) {
                _downloadsStorage.push(downloads[index])
            } else {
                _downloadsStorage.push(download)
            }
        })
        this.setState({downloadsStorage: _downloadsStorage})
    }
    //
    addHistory = (item) => {
        const {historyList} = this.state;
        const index = historyList.findIndex(el => el.id === item.id);
        if (index >= 0) {
            historyList.splice(index, 1);
        }
        this.setState({historyList: [item, ...historyList]});
    }
    //
    removeHistory = (idList) => {
        const {historyList} = this.state;
        const _historyList = historyList.filter(el => !idList.find(d => d === el.id));
        this.setState({historyList: _historyList});
    }

    //
    findHistory = (id) => {
        const {historyList} = this.state;
        return historyList.find(el => el.id === id);
    }


    saveLink = (links) => {
        let context = this
        const {savedLinks} = this.state;
        links = (Array.isArray(links) ? links : [links]).filter(function (link) {
            return !context.getLink(link)
        })
        this.setState({savedLinks: savedLinks.concat(links)});
    }
    getLink = (link) => {
        const {savedLinks} = this.state
        let result = link ? savedLinks.some(item => item === link) : false
        return result
    }


    saveLinks(savedLinks) {
        let context = this
        localStorage.setItem(StoreKeyLinks, JSON.stringify(savedLinks))
        context.setState({
            savedLinks: savedLinks,
        })
    }

    async componentDidMount() {
        let context = this
        let savedLinks = JSON.parse(localStorage.getItem(StoreKeyLinks) || '[]')
        context.saveLinks(savedLinks)
        const downloadsStorage = localStorage.getItem('downloadsStorage') || [];
        this.setState({downloadsStorage: downloadsStorage});
        const showImage = localStorage.getItem('showImage') || [];
        this.setState({showImage: showImage});


    }


    componentDidUpdate(prevProps, prevState) {
        let context = this
        if (prevState.savedLinks !== context.state.savedLinks) {
            context.saveLinks(context.state.savedLinks)
        }
        if (prevState.downloadsStorage !== this.state.downloadsStorage) {
            localStorage.setItem('downloadsStorage', context.state.downloadsStorage);
        }
        if (prevState.showImage !== this.state.showImage) {
            localStorage.setItem('showImage', context.state.showImage);
        }
    }

    render() {
        // let context = this
        const {savedLinks, historyList, showWKY, showImage, downloadsStorage} = this.state;
        return (
            <Store.Provider value={{
                savedLinks: savedLinks,
                downloadsStorage: downloadsStorage,
                refreshDownloads: this.refreshDownloads,
                addDownload: this.addDownload,
                removeDownloads: this.removeDownloads,
                saveLink: this.saveLink,
                getLink: this.getLink,
                historyList: historyList,
                addHistory: this.addHistory,
                removeHistory: this.removeHistory,
                findHistory: this.findHistory,
                toggleWKY: this.toggleWKY,
                showWKY: showWKY,
                toggleImage: this.toggleImage,
                showImage: showImage,
            }}>
                {this.props.children}
            </Store.Provider>
        )
    }
}