import React, {Component} from 'react';
import './MoviePageTemplate.css';
import MovieFetcherTemplate from './MovieFetcherTemplate'
// import {RenderWKY} from "./WKYManagerV2";
import {Store} from "./StoreProvider";
import Icon from 'antd/lib/icon'
// import md5 from "react-native-md5";
// import DownloadsInfoManager from "./DownloadsInfoManager";
import GiteeManagerView, {GiteeManagerHelper, GiteeManager} from './GiteeManager'
import LEUtils from '../LEFrameWorks/LEUtils'
import notification from 'antd/lib/notification'
import Button from 'antd/lib/button'
import BackTop from 'antd/lib/back-top'
import Badge from 'antd/lib/badge'

const ButtonConfigs = {
    image: {
        id: 'image',
        key: 'showImage',
        isContext: true,
        iconH: 'file-image',
        iconN: 'file-image',
        icon: Icon,
    },
    text: {
        id: 'text',
        key: 'showText',
        isContext: false,
        iconH: 'font-size',
        iconN: 'font-size',
        icon: Icon,
    },
    scroll: {
        id: 'scroll',
        key: 'toggleScroll',
        isContext: false,
        iconH: 'pic-center',
        iconN: 'pic-center',
        icon: Icon,
    },
    server: {
        id: 'server',
        key: 'toggleServer',
        isContext: false,
        iconH: 'database',
        iconN: 'database',
        icon: Icon,
    },
    visible: {
        id: 'visible',
        key: 'visible',
        isContext: false,
        iconH: 'eye',
        iconN: 'eye-invisible',
        icon: Icon,
    },
    history: {
        id: 'history',
        key: 'history',
        isContext: false,
        iconH: 'history',
        iconN: 'history',
        icon: Icon,
    },
    helper: {
        id: 'helper',
        key: 'giteeHelperVisible',
        isContext: false,
        iconH: 'info',
        iconN: 'info',
        icon: Icon,
    },
}
let ConfigURL = 'https://gitee.com/larryemerson/PersonalConfigs/raw/master/PMConfigs.json'
const ContentType = {
    Text: 0,
    Image: 1,
    Test: -1,
}
const MaxLoadingFailed = 3
const GiteeConfigsKey = 'GiteeConfigsKey'

export default class MoviePageTemplate extends Component {

    componentWillUnmount() {
    }

    componentWillMount() {
        let context = this
        console.log('GiteeConfigsKey',LEUtils.getFromLocal(GiteeConfigsKey))
        if (context.state.currentPages.length > 0) {
            context.setState({
                selectedPage: context.state.currentPages.length > 0 ? context.state.currentPages[0] : undefined
            })
        } else {
            context.state[ButtonConfigs.text.key] = LEUtils.getFromLocal(ButtonConfigs.text.key) == undefined ? LEUtils.getFromLocal(ButtonConfigs.text.key) : true
            context.state[ButtonConfigs.visible.key] = LEUtils.getFromLocal(ButtonConfigs.visible.key) == undefined ? LEUtils.getFromLocal(ButtonConfigs.visible.key) : false
            // false &&
            context.getGitee(() => {
                LEUtils.request('', undefined, ConfigURL).success(function (status, response) {
                    response.json().then(function (json) {
                        context.state.currentPages = json.Source || []
                        let pending = []
                        context.giteeConfigs.forEach(config => {
                            if (!context.state.currentPages.some(s => {
                                    return s.config == config.config
                                })) {
                                pending.push(config)
                            }
                        })
                        context.state.currentPages = context.state.currentPages.concat(pending)
                        context.onChangePageIndex(context.state.currentPages.length > 0 ? 0 : undefined)
                    })
                }).error(function (e) {
                    console.log('test error', e)
                }).get()
            })
        }

    }

    getGitee(callback) {
        let context = this;
        context.giteeConfigs = LEUtils.getFromLocal(GiteeConfigsKey) || []
        callback && callback()
    }

    componentDidMount() {
        let context = this;
    }


    constructor() {
        super();
        this.viewabilityConfig = {
            minimumViewTime: 200,
            waitForInteraction: true,
            // viewAreaCoveragePercentThreshold: 50,
            itemVisiblePercentThreshold: 95,
        }
        this.state = {
            dataSourceRendered: [],
            isRefreshing: false,
            autoScrollPixels: 1,
            autoScrollEnabled: 0,
            pastesArray: [],
            togglePaste: false,
            //
            currentPages: [],
            selectedPage: undefined,
            contentOffset: 0,
            inputServer: undefined,
            inputServer_: undefined,
            previewImages: [],
            previewImageIndex: 0,
            screenWidth: '100%',
            screenHeight: '100%',
            togglePageSelection: true,
        };
        this.loadingFailed = 0
        this.fetchers = {}
        this.requestConfig = undefined
        this.giteeConfigs = []
    }

    onChangePageIndex(index) {
        let context = this
        if (context.state.selectedPage != index && index != undefined) {

            context.loadingFailed = 0
            context.state.selectedPage = index
            context.state.togglePageSelection = false
            context.requestConfig = context.state.currentPages[context.state.selectedPage]
            context.loadNews()
        }

    }

    loadNews() {
        let context = this;
        context.state.dataSourceRendered = []
        context.loadMore()
    }

    loadMore() {
        let context = this;
        if (context.state[ButtonConfigs.history.key]) {
            let folder = context.getFolder(context.requestConfig.config)
            context.historyFetch = context.historyFetch || new MovieFetcherTemplate({config: ''})
            context.historyFetch.getDetails((data) => {
                if (data) {
                    let ds = []
                    let texts = data.texts || []
                    let images = data.images || []
                    let hrefs = data.hrefs || []
                    let links = hrefs && hrefs.reduce((result, {title, href}) => {
                        result[title] = href
                        return result
                    }, {})
                    images.forEach(i => {
                        ds.push({
                            type: ContentType.Image,
                            content: i,
                            config: data.config,
                        })
                    })
                    ds.push({
                        type: ContentType.Text,
                        content: {
                            texts,
                            links,
                        }
                    })
                    context.setState({
                        dataSourceRendered: context.state.dataSourceRendered.concat(ds),
                        isRefreshing: false,
                    }, () => {
                        context.listView && context.listView.flashScrollIndicators()
                    })
                } else {
                    context.loadingFailed++
                    notification.error({
                        message: ''
                    })
                    // LEToast.fail('')
                    context.setState({
                        isRefreshing: false,
                    })
                }
            }, folder)
            return
        }
        context.setState({
            isRefreshing: true,
        }, () => {
            if (context.requestConfig && context.loadingFailed < MaxLoadingFailed) {
                context.fetchers[context.requestConfig.config] = context.fetchers[context.requestConfig.config] || new MovieFetcherTemplate(context.requestConfig)
                context.fetchers[context.requestConfig.config].getDetails((data) => {
                    if (data) {
                        let ds = []
                        let texts = data.texts || []
                        let images = data.images || []
                        let hrefs = data.hrefs || []
                        let links = hrefs && hrefs.reduce((result, {title, href}) => {
                            result[title] = href
                            return result
                        }, {})
                        images.forEach(i => {
                            ds.push({
                                type: ContentType.Image,
                                content: i,
                                config: data.config,
                            })
                        })
                        ds.push({
                            type: ContentType.Text,
                            content: {
                                texts,
                                links,
                            }
                        })
                        context.setState({
                            dataSourceRendered: context.state.dataSourceRendered.concat(ds),
                            isRefreshing: false,
                        }, () => {
                            context.listView && context.listView.flashScrollIndicators()
                        })
                    } else {
                        context.loadingFailed++
                        notification.error({
                            message: ''
                        })
                        context.setState({
                            isRefreshing: false,
                        })
                    }
                }, context.state[ButtonConfigs.history.key])
            }
        })
    }

    handleDownload(link, title) {
        let context = this
        const {addDownload} = context.context;
        addDownload({
            title,
            link,
            addTime: new Date(),
            copyTime: '',
            deleteTime: '',
            checked: false,
            coppied: false,
            deleted: false,
            // id: md5.hex_md5(title + '_' + link),
        })
    }

    getFolder(f) {
        let folder = '' + f
        folder = folder.replace('.json', '')
        folder = folder.substr(folder.lastIndexOf('/') + 1)
        return folder
    }

    render() {
        let context = this
        let line = 0
        const {
            showImage,
            downloadsStorage,
            toggleWKY,
            showWKY,
            currentOrientation,
        } = context.context;
        let pageX = parseInt(!currentOrientation ? context.state.screenWidth : context.state.screenHeight)
        let pageY = parseInt(currentOrientation ? context.state.screenWidth : context.state.screenHeight)
        let renderBtn = (text, callback) => {
            return <Button
                type={'primary'}
                icon={text}
                className="center"
                onClick={() => callback && callback()}
            />
        }
        let renderStepper = (title, key, step, callback) => {
            return <div className="step-container">
                {renderBtn('minus', () => {
                    context.state[key] -= step * 100
                    context.state[key] = Math.max(context.state[key], 0)
                    callback && callback()
                })}
                {renderBtn('minus', () => {
                    context.state[key] -= step * 10
                    context.state[key] = Math.max(context.state[key], 0)
                    callback && callback()
                })}
                {renderBtn('minus', () => {
                    context.state[key] -= step
                    context.state[key] = Math.max(context.state[key], 0)
                    callback && callback()
                })}
                <div className="step-text-container">
                    <div className="step-text" style={{fontWeight: 'bold'}}>{title}</div>
                    <div className="step-text">{context.state[key]}</div>
                </div>
                {renderBtn('plus', () => {
                    context.state[key] += step
                    callback && callback()
                })}
                {renderBtn('plus', () => {
                    context.state[key] += step * 10
                    callback && callback()
                })}
                {renderBtn('plus', () => {
                    context.state[key] += step * 100
                    callback && callback()
                })}
            </div>
        }
        let renderItem = ({item, itemIndex}) => {
            const {type, content, config} = item
            // console.log({type, content})
            return type == ContentType.Image && showImage ? <div
                className="item-img-container"
                key={itemIndex + '_' + type}
                onClick={() => {
                    let images = []
                    context.state.dataSourceRendered.forEach((item) => {
                        if (item.type == ContentType.Image) {
                            images.push({url: item.content})
                            if (item.content == content) {
                                context.state.previewImageIndex = images.length - 1
                            }
                        }
                    })
                    context.setState({
                        previewImages: images,
                    })
                }}
            >
                {/*<img className="item-img" src={content}/>*/}
                <img className="item-img"/>
            </div> : type == ContentType.Text && context.state[ButtonConfigs.text.key] ?
                <div
                    key={itemIndex + '_' + type}
                    style={{marginTop: 15, marginBottom: 20}}
                >
                    {content.texts.map(function (text, idx) {
                        let link = content.links[text]
                        return link ? <div
                                key={idx}
                                className="item-text-container"
                                onClick={() => {
                                    context.handleDownload(link, text)
                                }}
                            >
                                <div className="item-text">{text}</div>
                            </div> :
                            <div key={idx} className="item-summary">{text}</div>
                    })}
                </div> : null
        }
        return (
            <div className="mainRoot">
                <div className="list">
                    {context.state.dataSourceRendered.map(function (item, itemIndex) {
                        return renderItem({item, itemIndex})
                    })}
                </div>
                {downloadsStorage.length > 0 ? <div style={{
                    position: 'absolute',
                    top: '15px',
                    left: '10px',
                }}>
                    <Badge count={downloadsStorage.length} showZero>
                        <Button
                            shape="circle"
                            type={'primary'}
                            icon={'download'}
                            onClick={() => context.setState({togglePaste: true})}
                        />
                    </Badge>
                </div> : null}
                {/*<DownloadsInfoManager*/}
                {/*visible={context.state.togglePaste}*/}
                {/*onClose={() => {*/}
                {/*context.setState({*/}
                {/*togglePaste: false,*/}
                {/*})*/}
                {/*}}*/}
                {/*/>*/}
                {context.state[ButtonConfigs.scroll.key] ? <div
                    style={{
                        absolute: 'position',
                        // left:'10px',
                        bottom: '60px',//Config.getScreenHeight() * 2 / 5,
                        width: '100%',
                        height: '20%',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                    onClick={() => {
                        context.state[ButtonConfigs.scroll.key] = false
                        context.setState({})
                    }}
                >
                    <div
                        style={{
                            marginHorizontal: '15px',
                            borderRadius: '6px',
                            paddingHorizontal: '8px',
                            paddingVertical: '5px',
                            backgroundColor: '#888',
                        }}
                    >
                        {renderStepper('', 'autoScrollEnabled', 1, () => {
                            context.setState({
                                autoScrollEnabled: context.state.autoScrollEnabled
                            }, () => {
                                context.state.autoScrollEnabled > 0 ? context.startAutoScroll() : context.stopAutoScroll()
                            })
                        })}
                        {renderStepper('', 'autoScrollPixels', 1, () => {
                            context.setState({
                                autoScrollPixels: context.state.autoScrollPixels
                            })
                        })}
                    </div>
                </div> : null}
                <GiteeManagerView
                    ref={ref => context.gitee = ref}
                    visible={context.state[ButtonConfigs.server.key]}
                    invisible={() => {
                        context.state[ButtonConfigs.server.key] = false
                        context.setState({})
                    }}
                />
                {/*<RenderWKY*/}
                {/*show={showWKY}*/}
                {/*open={toggleWKY}*/}
                {/*onExit={() => {*/}
                {/*toggleWKY && toggleWKY()*/}
                {/*}}*/}
                {/*/>*/}
                <GiteeManagerHelper
                    visible={context.state[ButtonConfigs.helper.key]}
                    invisible={() => {
                        context.state[ButtonConfigs.helper.key] = false
                        context.setState({})
                    }}
                />
                {context.state.togglePageSelection ? <div className="menu-page">
                    <div className="menu-page-list">
                        {context.state.currentPages.map(function (item, index) {
                            return item.valid || context.state[ButtonConfigs.visible.key] ? <Button
                                type={'ghost'}
                                style={{
                                    width: '100%',
                                    paddingVertical: '10px',
                                    backgroundColor: index == context.state.selectedPage ? '#9e6946' :
                                        ++line % 2 == 0 ? 'rgba(200,200,200,0.4)' : 'rgba(255,255,255,0.4)',
                                    paddingLeft: '15px',
                                    justifyContent: 'center',
                                }}
                                key={index}
                                onClick={() => {
                                    context.onChangePageIndex(index)
                                }}
                            >
                                <div style={{
                                    color: index == context.state.selectedPage ? '#ff6515' :
                                        line % 2 == 0 ? 'rgba(250,250,250,0.8)' : 'rgba(220,220,220,0.8)'
                                }}>{item.title}</div>
                            </Button> : null
                        })}
                    </div>
                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'center',
                            paddingLeft: '10px',
                            width: '100%',
                            height: '40px',
                            backgroundColor: 'rgba(255,255,255,0.3)',
                        }}
                    >
                        <div style={{flexDirection: 'row', display: 'flex'}}>
                            {Object.values(ButtonConfigs).map(function (config, index) {
                                let ctx = config.isContext ? context.context : context.state
                                return <Button
                                    type={ctx[config.key] ? 'danger' : 'primary'}
                                    key={'ButtonConfigs' + index}
                                    style={{
                                        paddingHorizontal: 5,
                                        paddingVertical: 5,
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        marginRight: 10,
                                    }}
                                    onClick={() => {
                                        context.onButton(config.id)
                                    }}
                                    icon={ctx[config.key] ? config.iconH : config.iconN}
                                />
                            })}
                        </div>
                    </div>
                </div> : null}
                <Button
                    className={'menu-btn'}
                    type={context.state.togglePageSelection ? 'danger' : 'primary'}
                    shape="circle"
                    icon={context.state.togglePageSelection ? 'menu-unfold' : 'menu-fold'}
                    onClick={() => {
                        context.state[ButtonConfigs.server.key] = false
                        context.state[ButtonConfigs.scroll.key] = false
                        context.setState({
                            togglePageSelection: !context.state.togglePageSelection,
                        })
                    }}
                />
                {context.state.contentOffset == 0 ? null : <div
                    style={{
                        position: 'absolute',
                        left: 15,
                        width: 32,
                        height: 32,
                        borderRadius: 16,
                        borderWidth: 2,
                        borderColor: '#fee',
                        alignItems: 'center',
                        justifyContent: 'center',
                        backgroundColor: 'rgba(0,0,0,0.6)',
                    }}
                    onClick={() => {
                        context.setState({
                            contentOffset: 0,
                        })
                    }}
                ><Icon type={'arrowup'} size={23} color={'#fee'}/></div>}
                {/*{context.state.previewImages.length > 0 ? <ImageViewer*/}
                {/*style={{*/}
                {/*position: 'absolute',*/}
                {/*width: '100%',*/}
                {/*height: '100%',*/}
                {/*}}*/}
                {/*backgroundColor={'rgba(0,0,0,0.5)'}*/}
                {/*imageUrls={context.state.previewImages}*/}
                {/*index={context.state.previewImageIndex}*/}
                {/*onClick={() => {*/}
                {/*context.state.previewImages = []*/}
                {/*context.setState({})*/}
                {/*}}*/}
                {/*/> : null}*/}
                <BackTop/>
            </div>)
    }

    onButton(id) {
        let context = this
        const {
            toggleImage,
        } = context.context;
        let config = ButtonConfigs[id]
        switch (config.id) {
            case ButtonConfigs.visible.id: {
                context.state[config.key] = !context.state[config.key]
                context.setState({})
            }
                break
            case ButtonConfigs.text.id: {
                context.state[config.key] = !context.state[config.key]
                context.setState({}, () => {
                    LEUtils.saveToLocal(config.key, context.state[config.key])
                })
            }
                break
            case ButtonConfigs.image.id: {
                toggleImage && toggleImage()
            }
                break
            case ButtonConfigs.scroll.id: {
                context.state[config.key] = true
                context.setState({
                    togglePageSelection: false,
                })
            }
                break
            case ButtonConfigs.server.id: {
                context.state[config.key] = true
                context.setState({
                    togglePageSelection: false,
                }, () => {
                    context.gitee.getConfigFile((result) => {
                        // console.log('>>>>>', {result})
                        let pending = []
                        let kv = {}
                        let kv2 = {}
                        context.state.currentPages.forEach((s, index) => {
                            kv[s.config] = index
                        })
                        context.giteeConfigs.forEach((s, index) => {
                            kv2[s.config] = index
                        })
                        result.forEach((s) => {
                            let key = kv[s.config]
                            if (key >= 0) {
                                context.state.currentPages[key] = s
                            } else {
                                pending.push(s)
                            }
                        })
                        result.forEach((s) => {
                            kv2[s.config] = s
                        })
                        context.giteeConfigs = Object.values(kv2)
                        LEUtils.saveToLocal(GiteeConfigsKey, context.giteeConfigs)
                        context.setState({
                            currentPages: context.state.currentPages.concat(pending)
                        })
                    })
                })
            }
                break
            case ButtonConfigs.helper.id: {
                context.state[config.key] = true
                context.setState({
                    togglePageSelection: false,
                })
            }
                break
            case ButtonConfigs.history.id: {
                context.state[config.key] = !context.state[config.key]
                context.setState({
                    togglePageSelection: false,
                }, () => {
                    context.loadNews()
                })
            }
                break
        }
    }

    startAutoScroll() {
        let context = this
        context.stopAutoScroll()
        if (context.state.autoScrollEnabled > 0) {
            context.autoScrollTimer = setInterval(() => {
                context.autoScroll()
            }, context.state.autoScrollEnabled)
        }
    }

    stopAutoScroll() {
        let context = this
        context.autoScrollTimer && clearInterval(context.autoScrollTimer)
    }

    autoScroll() {
        let context = this
        // if (context.listView && context.state.contentOffset < context.contentHeight - Config.ScreenHeight) {
        //     let offset = context.state.contentOffset + context.state.autoScrollPixels
        // context.listView.scrollToOffset({offset: offset})
        // if (context.state.contentOffset < 10) {
        //     context.setState({
        //         contentOffset: offset
        //     })
        // }
        // }
    }
}
MoviePageTemplate.contextType = Store;